export default Vue.extend({
  name: "ApplicationMenu",

  props: {
    value: {
      type: Boolean,
      required: true,
    },
  },

  methods: {
    setDrawer(value: boolean) {
      this.$emit("input", value);
    },

    lookup(name: string) {
      if (name === null || !name.startsWith("Feature")) {
        return name;
      }
      return this.$vuetify.lang.t(`$vuetify.${name}`);
    },
  },
});
